body {
    margin: auto;
    width: 500px;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
        "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}

button {
    margin-left: 5px;
}

div {
    margin: 10px 0 10px 4px;
}

.error {
    color: red;
    background: wheat;
    border: solid;
    font-size: 20px;
    border-radius: 10px;
    padding: 3px 5px 6px;
    margin-bottom: 20px;
}

.success {
    color: green;
    background: lightgreen;
    border: solid;
    font-size: 20px;
    border-radius: 10px;
    padding: 3px 5px 6px;
    margin-bottom: 20px;
}

.person {
    color: grey;
    margin: 10px 0;
    list-style: square;
}

h2.title {
    text-align: center;
    font-size: xx-large;
}
